/**
 * formatClientName.
 */
const clients = {
  0: 'APP',
  1: 'APP',
  2: 'APP',
  3: 'APP',
  4: 'APP',
  5: 'APP',
  6: 'APP',
};

const formatClientName = clientCode => clients[clientCode] || clientCode;

export default formatClientName;


