import React, { Component } from 'react';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import {List, ListItem} from 'material-ui/List';
import Avatar from 'material-ui/Avatar';
import Divider from 'material-ui/Divider';
import NavigationChevronRight from 'material-ui/svg-icons/navigation/chevron-right';
import AppBar from './AppBar.jsx';
import ToolBar from './tool-bar.jsx';
import FindBanner from './find-banner.jsx';

import find_weiba from '../app/images/find/weiba.png';
import find_topic from '../app/images/find/topic.png';
import find_channel from '../app/images/find/channel.png';

class Find extends Component
{
  constructor(props) {
    super(props);
  }

  componentDidMount() {
    appNode.style.display = 'block';
    $('.mdl-layout__container').hide();
  }

  componentWillReceiveProps() {
    this.componentDidMount();
  }

  render() {
    return (
      <MuiThemeProvider muiTheme={muiTheme}>
        <div style={styles.root}>
          <AppBar
            title={<h1 className="center"></h1>}
            showMenuIconButton={false}
            zDepth={0}
          />
          <FindBanner />
          <Divider />
          <List
            style={styles.List}
          >
            <ListItem
              leftAvatar={<Avatar src={find_weiba} />}
              rightIcon={<NavigationChevronRight />}
              primaryText={' Weiba'}
              secondaryText={''}
              onTouchTap={() => {
                this.context.router.push('/weiba');
              }}
            />
            <Divider />
            <ListItem
              leftAvatar={<Avatar src={find_topic} />}
              rightIcon={<NavigationChevronRight />}
              primaryText={' Topic'}
              secondaryText={''}
              onTouchTap={() => {
                this.context.router.push('/topic');
              }}
            />
            <Divider />
            <ListItem
              leftAvatar={<Avatar src={find_channel} />}
              rightIcon={<NavigationChevronRight />}
              primaryText={' Channel'}
              secondaryText={''}
              onTouchTap={() => {
                this.context.router.push('/channel');
              }}
            />
            <Divider />
          </List>
          <ToolBar value={'find'} />
        </div>
      </MuiThemeProvider>
    );
  }
}

const styles = {
  root: {
    boxSizing: 'border-box',
    width: '100%',
    minHeight: '100%',
    paddingTop: 50,
    paddingBottom: 50,
    // backgroundColor: '#f1f1f1',
  },
  List: {
    paddingTop: 0,
    paddingBottom: 0,
    backgroundColor: '#fff',
  }
};

Find.contextTypes = {
    router: React.PropTypes.object.isRequired
};

export default Find;



